<template>
  <Editor :content="getDocument" />
</template>

<script>
import { mapGetters } from "vuex";
import Editor from "@/components/AceEditor.vue";

export default {
  components: {
    Editor
  },
  computed: {
    ...mapGetters(["getDocument"])
  },
  created() {
    this.$store.dispatch("fetchDocument", this.$route.params);
  }
};
</script>